import React, { useEffect, useState } from 'react';
import { Box, Button, Container, TextField, Table, TableHead, TableBody, TableRow, TableCell } from '@material-ui/core'
import './style.css'

function CalculadoraIMC() {
    const [peso, setPeso] = useState('');
    const [altura, setAltura] = useState('');
    const [resultado, setResultado] = useState('');

    useEffect(() => {
        if (resultado > 0) {
            alterar();
        }
    })

    function calculaIMC(peso, altura) {
        const resultado = peso / (altura * altura);
        return resultado.toFixed(2);
    }
    
    function alterar() {
        limpar();
        const linha = selecionarTr()
        linha.classList.add('destaque')
    }

    function selecionarTr() {
        if (resultado <= 16.9) {
            return document.getElementById('map');
        } else if (resultado <= 18.49) {
            return document.getElementById('abp');
        } else if (resultado <= 24.99) {
            return document.getElementById('pn');
        } else if (resultado <= 29.99) {
            return document.getElementById('ap');
        } else if (resultado <= 34.99) {
            return document.getElementById('o1');
        } else if (resultado <= 40) {
            return document.getElementById('o2');
        } else {
            return document.getElementById('o3');
        }
    }

    function limpar() {
        const linhas = document.getElementsByTagName('tr')
        const array = Array.from(linhas)
        array.forEach((linha) => {
            linha.classList.remove('destaque');
        })
    }

    return (
        <Container
            maxWidth="sm"
        >
            <form
                onSubmit={(event) => {
                    event.preventDefault();
                    setResultado(calculaIMC(peso, altura));
                }}>
                <Box
                    textAlign="center"
                    px={5}
                    mt={10}
                >
                    <TextField
                        onChange={(event) => {
                            let peso = event.target.value;
                            peso = peso.replace("-", "");
                            setPeso(peso);
                        }}
                        value={peso}
                        id="peso"
                        label="Peso"
                        variant="outlined"
                        type="number"
                        required
                        fullWidth
                    />
                    <Box
                        mt={2}
                    >
                        <TextField
                            onChange={(event) => {
                                let altura = event.target.value;
                                altura = altura.replace("-", "");
                                setAltura(altura)
                            }}
                            value={altura}
                            id="altura"
                            label="Altura"
                            variant="outlined"
                            type="number"
                            required
                            fullWidth
                        />
                    </Box>
                </Box>
                <Box
                    textAlign="center"
                    px={5}
                    mt={2}
                >
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        fullWidth>
                        Calcular IMC
                    </Button>
                </Box>
            </form >
            <Box textAlign="center" mt={3}>
                <label id="resultado"> {`Resultado: ${resultado}`} </label>
            </Box>
            <Box px={5} mt={2}>
                <Table size="small">
                    <TableHead>
                        <TableRow>
                            <TableCell >Classificação</TableCell>
                            <TableCell align="center">IMC</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow key="map" id="map">
                            <TableCell >Muito abaixo do peso</TableCell>
                            <TableCell align="center">{"<"} 16.9</TableCell>
                        </TableRow>
                        <TableRow key="abp" id="abp">
                            <TableCell >Abaixo do peso</TableCell>
                            <TableCell align="center">17 a 18.4</TableCell>
                        </TableRow>
                        <TableRow key="pn" id="pn">
                            <TableCell >Peso normal</TableCell>
                            <TableCell align="center">18.5 a 24.9</TableCell>
                        </TableRow>
                        <TableRow key="ap" id="ap">
                            <TableCell >Acima do peso</TableCell>
                            <TableCell align="center">25 a 29.9</TableCell>
                        </TableRow>
                        <TableRow key="o1" id="o1">
                            <TableCell >Obesidade Grau 1</TableCell>
                            <TableCell align="center">30 a 34.9</TableCell>
                        </TableRow>
                        <TableRow key="o2" id="o2">
                            <TableCell >Obesidade Grau 2</TableCell>
                            <TableCell align="center">35 a 40</TableCell>
                        </TableRow>
                        <TableRow key="o3" id="o3">
                            <TableCell >Obesidade Grau 3</TableCell>
                            <TableCell align="center">{">"} 40</TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </Box>
        </Container>
    )
}

export default CalculadoraIMC;